/*
 * loading组件
 * @Author: zhaoye 
 * @Date: 2017-01-12 17:28:25 
 * @Last Modified by: zhaoye
 * @Last Modified time: 2017-07-22 21:34:43
 */
<template>
    <div class="loading-container">
        <div class="loading-wrapper" v-show="show">
            <div class="loading loading_play" >            
            </div>
        </div>
    </div>
</template>
<script>
    import Vue from 'vue';
    import base from '../base.vue';
    export default Vue.extend({
        mixins: [base],
        data () {
            return {
                show: true
            }
        },
        created () {
            this.mountMyself2();
            if(this.mode && this.mode == 'lazy'){
                this.show = false
                setTimeout(() => {
                    this.show = true;
                }, 300)
            }
        },
    })
    
</script>
<style lang='less'>
    @import '../less/var.less';
    @import '../less/layout.less';
    .loading-container {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: @z-max * 2;
        .flexbox();
        .flexbox.center();
        .loading-wrapper{
            background-color: rgba(0,0,0,.5);
            padding: .06rem;
            border-radius: .1rem;
            .loading {
                font-size: @font-nm;
                // padding: .2rem;
                /*background-color: rgba(0, 0, 0, 0.7);*/
                /*border-radius: .04rem;*/
                /*max-width: 80%;*/
                color: @white;
                text-align: center;
                word-break: break-all;
                line-height: 1.3;
                width: .46rem;
                height: .46rem;
                background-image: url(./images/sprite.png);
                background-repeat: no-repeat;
                background-position: 0 0;
                -webkit-background-size: .46rem 6.9rem;
                background-size: .46rem 6.9rem;
            }
        }
    }
    .loading_play{
        -webkit-animation: mui-loading-ani .6s infinite;
    }
    .position (@i) {
        background-position-y: -.46rem * @i;
    }
    @-webkit-keyframes mui-loading-ani {
        0%,6.66% {
            background-position-y: 0;
        }

        6.67%,13.32% {
            .position (1);
        }

        13.33%,19.98% {
            .position (2);
        }

        19.99%,26.64%{
            .position (3);
        }

        26.65%,33.3% {
            .position (4);
        }

        33.31%,39.96%{
            .position (5);
        } 

        39.97%,46.62% {
            .position (6);
        }

        46.63%,53.28%{
            .position (7);
        }

        53.29%,59.94%  {
            .position (8);
        }

        59.95%,66.6%{
            .position (9);
        }
        66.62%,73.26%{
            .position (10);
        }
        73.27%,79.92% {
            .position (11);
        }
        79.93%,86.58% {
            .position (12);
        }
        86.59%,93.24% {
            .position (13);
        }
        93.25%,99.9% {
            .position (14);
        }
        99.91%,100% {
            .position (15);
        } 
    }
</style>


