<template>
    <div class="scroller-container-wrapper" :class="[direction, className]">
        <div class='scroller-container' :class="[direction, className]">
            <div class='scroller'
            :style='[transform,transition]'
            :class="className"
            @touchstart ='touchstart'
            @touchmove ='touchmove'
            @touchend ='touchend'
            @transitionEnd ='transitionEnd'
                >
                <slot></slot>
            </div>
        </div>
    </div>
</template>
<script>
import swiperMixin from "../swiper/swiper.mixin.vue";
import base from '../base.vue';
export default {
    props: ['dontDrag','class','direction', 'className'],
    mixins: [swiperMixin],
    data () {
        return {
            maxTranslate: 0,
            contentLimit: 0,
            $container: null,
            $scroller: null,
            wrapperLimit: 0,
            iam: 'scroller'
        }
    },
    created () {
        this.initSwiper()
        this.$on('scroll', (e) => {
            this.scroll(e);
        });
        this.$on('scrollEnd', (e) => {
            this.scrollEnd(e);
        });
    },
    mounted () {
        this.$container = this.$el;
        this.$scroller = this.$el.childNodes[0].childNodes[0];
        this.computeTransLimit();
    },
    methods: {
        reset () {
            this.translate = 0;
        },
        scroll (e) {
            if(this.dontDrag)return;
            this.computeTransLimit();
            if(this.contentLimit < this.wrapperLimit)return;
            if(!this.scrolling)return;
            //e.stopPropagation();
            if(this.translate < -this.maxTranslate || this.translate > 0){
                this.translate += this.delta/2.5
            }else{
                this.translate += this.delta
            }
        },
        scrollEnd () {
            if(this.dontDrag)return;
            if(this.translate > 0){
                this.translate = 0
            }
            if(this.translate < -this.maxTranslate){
                this.translate = -this.maxTranslate
            }
        },
        _getMargin ($el) {
            if(this.direction == 'horizontal'){
                return $el.offsetWidth
                     + (parseFloat($el.style.marginLeft) || 0) 
                     + (parseFloat($el.style.marginRight) || 0)
            }else{
                return $el.offsetHeight
                     + (parseFloat($el.style.marginTop) || 0) 
                     + (parseFloat($el.style.marginBottom) || 0)
            }
        },
        computeTransLimit () {
            let children = this.$scroller.children;
            this.contentLimit = 0;
            this.wrapperLimit = this._getMargin(this.$container);
            
            for(let i=0; i<children.length; i++){
                this.contentLimit += this._getMargin(children[i]);
            }
            if(this.contentLimit < this.wrapperLimit)
                this.maxTranslate = 0;
            else
                this.maxTranslate = this.contentLimit - this.wrapperLimit;
        },
        scrollTo ({x=this.translateX, y=this.translateY}) {
            //开启缓动
            this.easing = true

            //计算位移值
            let translate
            this.direction === 'horizontal' ? (translate = x) : (translate = y)
            
            //计算边界
            this.computeTransLimit()
            
            //赋值
            this.translate = translate
            
            //修正
            if(this.translate > 0){
                this.translate = 0
            }
            if(this.translate < -this.maxTranslate){
                this.translate = -this.maxTranslate
            }
        }
    }
};
</script>
<style lang='less'>
    @import '../less/layout.less';
    .scroller-container-wrapper {
        .flexbox();
        &.vertical {
            .flexbox.vertical();
        }
        position: relative;
        overflow: hidden;
        .scroller-container {
            &.vertical {
                position: relative;
                width: 100%;
                .scroller {
                    position: relative;
                    width: 100%;
                }
            }
            &.horizontal {
                .scroller {
                    display: -webkit-box;
                }
            }
            .scroller-container {
                position: relative;
                
            }
        }
    }
    
</style>



